import Vue from "vue";
import VueRouter from "vue-router";
import Home from "@/views/Home";
import Login from "@/views/Login";
import Tool from "@/views/Tool";
import About from "@/views/About";
import Article from "@/views/Article";

Vue.use(VueRouter);
const routes = [
    {
        path: "/",
        name: "Home",
        component: Home,
        meta: {
            title: "主頁",
        },
    },
    {
        path: "/login",
        name: "login",
        component: Login,
        meta: {
            title: "登入",
        },
    },
    {
        path: "/tool",
        name: "tool",
        component: Tool,
        meta: {
            title: "工具",
        },
    },
    {
        path: "/about",
        name: "about",
        component: About,
        meta: {
            title: "關於",
        },
    },
    {
        path: "/article/:id",
        name: "Detail",
        component: Article,
        meta: {
            title: "文章詳情頁",
        },
    },
    {
        path: "*",
        name: "NotFound",
        component: () => import("@/views/NotFound/404.vue"),
        meta: {
            title: "404 Not Found~",
        },
    },
];

const router = new VueRouter({
    scrollBehavior(to, from, savedPosition) {
        return {
            x: 0,
            y: 0,
        };
    },
    routes,
});

router.beforeEach((to, from, next) => {
    window.document.title =
        to.meta.title === undefined ? "默認標題" : to.meta.title;

    next();
});

export default router;
